@import "../../styles/main";

.contact {
    grid-column: 2 / -2;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 16px 16px;

    @include breakpoint-xs {
        padding: 0 32px 32px;
    }

    @include breakpoint-s {
        padding: 0 48px 48px;
    }

    @include breakpoint-m {
        padding: 0 64px 64px;
    }

    &__text {
        max-width: 800px;
        margin-bottom: 16px;
    }
}
